<template>
  <div>
    <div class='item'>
      <el-card shadow="hover" class='card'>
        <div class='top'>
          {{titleFormatted}}
        </div>

        <div class='middle'>
          {{detailFormatted}}
        </div>

        <div class='auth'>
          <span>{{userAuth}}</span>
          <span class='school'>{{schoolFormatted}}</span>
        </div>

        <div class='footer'>
          <span class='open' @click='showDetail(newsID)'>
            <span>{{$t('News.ShowDetail')}}</span>
            <i class='el-icon-caret-bottom'></i>
          </span>

          <span class='status'>
            <span>{{timeFormatted}}</span>
            <el-tooltip :content='this.comments + this.$t("News.CommentsAmount")' placement='top-start' effect='light'>
              <span class='statusElement'>
                <el-image :src="require('@/../public/assets/comment.png')" class='image'></el-image>{{comments}}
              </span>
            </el-tooltip>

            <el-tooltip :content='this.like + this.$t("News.LikeAmount")' placement='top-start' effect='light'>
              <span class='statusElement'>
                <el-image :src="require('@/../public/assets/like.png')" class='image'></el-image>{{like}}
              </span>
            </el-tooltip>
          </span>
        </div>
      </el-card>
    </div>
  </div>
</template>

<script>
export default {
  name: 'news-item',
  props: {
    newsID: { type: Number },
    title: { type: String },
    content: { type: String },
    userAuth: { type: String },
    time: { type: String },
    like: { type: Number },
    comments: { type: Number },
    school: { type: String },
  },
  computed: {
    titleFormatted () {
      if (this.title && this.title.length > 15) {
        return this.title.substring(0, 15) + '...';
      }
      return this.title;
    },
    detailFormatted () {
      if (this.content && this.content.length > 40) {
        return this.content.substring(0, 40) + '......';
      }
      return this.content;
    },
    usernameFormatted () {
      return this.formatUsername(this.username, this.isNicked);
    },
    schoolFormatted () {
      let schools = '';
      let arraySchool = JSON.parse(this.school);
      for (let i in arraySchool) {
        if (arraySchool[i] == 1) {
          schools += this.$t('User.UserSchool' + i) + ' ';
        }
      }
      return schools;
    },
    timeFormatted () {
      return this.formatTime(this.time);
    },
  },
  methods: {
    showDetail (newsID) {
      this.$bus.$emit('showNewsDetail', newsID)
    }
  }
}
</script>

<style scoped>
.item {
  margin-left: 10%;
  width: 80%;
}
.card {
  border-radius: 20px;
}
.top {
  font-size: 35px;
  text-align: left;
  margin-top: 10px;
  margin-left: 15px;
  font-weight: bolder;
}
.middle {
  font-size: 25px;
  text-align: left;
  margin-top: 20px;
  margin-left: 15px;
}
.auth {
  text-align: left;
  margin-top: 20px;
  margin-left: 15px;
}
.auth .school {
  margin-left: 20px;
}
.footer {
  text-align: left;
  margin-top: 20px;
  margin-left: 15px;
}
.status {
  float: right;
}
.statusElement {
  margin-left: 20px;
}
.status .image {
  width: 20px;
  position: relative;
  top: 5px;
}
.open {
  font-size: 20px;
  margin-top: 20px;
  margin-bottom: 10px;
  cursor: pointer;
  color: rgb(61, 87, 235);
}
</style>